import React, { Component } from 'react';
import {$http, Qs, DateF, toFix} from '../../tools';
import Toast from '../../common/toast';

const params = Qs();

 export default class loanProtocol extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loanList:{}
    }
  }
  componentDidMount() {
    $http.post('/cashman-web/service/loan/users/loan-orders/loancontract', {
            "contractType": "LOAN_AGREEMENT",
            "trdLoanOrderId": params.loanId,
            "userId":params.userId
        }).then(res=>{
         if(res.data && res.code== "00"){
          this.setState({
            loanList: res.data || {}
          }, ()=>{
          });
         }else{
           /*Toast.show(res.message);*/
         }
      }, err => {
          /*Toast.show("error");*/
      });

  }

  render() {
    document.title = '借款协议';
    const {loanList} = this.state;
    const appName = params.appName;
    return (
      <div className="protocol-wrap bgf7">
        <h2>借款（居间）协议 </h2>
        <p>编号：{loanList.loanOrderId}</p>
        <p>甲方（借款人）： {loanList.realName}</p>
        <p>身份证号码： {loanList.maskIdCardNum}</p>
        <p>乙方（出借人）：{loanList.partBRealName}</p>
        <p>丙方（居间方/ 担保方）：{loanList.companyTitle}</p>
        <p>丁方（居间方）：  {loanList.capitalName}</p>
        <p>借款人甲方通过丙方平台提出借款需求，丙方介绍甲方在丁方运营的融资平台向出借人乙方借款，乙方亦同意出借款项，丙方、丁方为甲方、乙方借款提供居间服务，现经各方协商一致，达成如下协议：</p>
        <p>释义：</p>
        <p>1、《借款协议》是指本协议（以下简称“借款协议”或“本协议”），其全部内容由甲、乙双方在线以类似点击确认方式视为认可并签署，并严格受其约束行使权利、履行义务。</p>
        <p>2、借款人（“甲方”）是指借款协议中列明的符合中华人民共和国法律规定的具有完全民事权利能力和民事行为能力，独立行使和承担本协议项下权利义务的个体。甲方为丙方平台上的注册用户，有借款需求。</p>
        <p>3、出借人（“乙方”）是指借款协议中列明的符合中华人民共和国法律规定的具有完全民事权利能力和民事行为能力，独立行使和承担本协议项下权利义务的个体。乙方为丁方平台上的注册用户，愿意出借合法取得的自有资金。</p>
        <p>4、居间方丙方（“丙方”）是指借款协议中列明的　　{loanList.companyTitle}公司（以下简称“{loanList.companyShortTitle} ”），其是一家在上海市合法成立并有效存续的有限责任公司，为甲方提供信息服务、媒介服务、合同管理、贷后管理等平台服务，并与甲方签订《平台服务协议》，丙方有权收取综合服务费（包括前期服务费与分期服务费）。丙方的综合服务费由丙丁双方根据约定，由丁方向甲方代收并支付给丙方。</p>
        <p>5、居间方丁方（“丁方”）是指借款协议中列明的　　{loanList.capitalName}公司，其是在　　{loanList.capitalCity}市 一家合法成立并有效存续的有限责任公司，为甲、乙双方的借款搭建平台，提供信息服务、合同管理等平台服务，丁方有权收取居间服务费。</p>
        <p>6、本协议项下甲方、乙方、丙方、丁方单独称“一方”，任意两方合称“双方”，任意三方合称“三方”，任意四方合称“各方”。</p>
        <p>7、工作日是指除国家法定节假日、公休日以外的丙方、丁方对外办理业务的任何一日。</p>
        <p>借款标的明细</p>
        <ul className="protocol-ul">
            <li>
                <span>本金金额</span>
                <span>{loanList.moneyAmount}</span>
            </li>
             <li>
                <span>大写</span>
                <span>{loanList.cnMoneyAmount}</span>
            </li>
             <li>
                <span>借款用途</span>
                <span> {loanList.loanPurpose}</span>
            </li>
             <li>
                <span>借款起息日</span>
                <span>{loanList.loanEndTime}</span>
            </li>
             <li>
                <span>到期还款日</span>
                <span>{loanList.loanTime}</span>
            </li>
            <li>
                <span>还款方式</span>
                <span>{loanList.paymentMethod}</span>
            </li>
            <li>
                <span>借款利息</span>
                <span>{loanList.interest}</span>
            </li>
        </ul>
        <h3>第一条 借款</h3>
        <p>1、借款标的各项相关内容以《借款标的明细》中列明的内容为准，甲方应当于约定的还款日支付《借款标的明细》中约定的各项费用，具体计算方式以甲方与丙方签订的《平台服务协议》为准</p>
        <p>2、借款期限是指《借款标的明细》中列明的借款起息日起至借款到期日。</p>
        <p>3、借款起息日是指丙方打款给甲方开始计算利息的日期</p>
        <p>4、还款日是指根据本协议约定甲方将还款转至指定还款账户之日，为借款到期日。</p>
        <p>5、乙方按照丁方平台的规则接受丁方平台发布的甲方的借款申请，对平台发布的投资计划类似点击“充值”按钮时，即视为乙方阅知并同意本协议（以电子形式生成），并向丁方发出不可撤销的授权指令，授权丁方委托其指定的第三方支付机构从乙方账户划扣出借资金，扣款金额等于本协议《借款标的明细》所列的借款本金金额。</p>
        <h3>第二条 正常还款</h3>
        <p>1、还款方式以《借款标的明细》中列明的方式为准，甲方须在还款日当日24:00前支付相当于《还款计划》中列明的包括本金、利息以及依照《平台服务协议》应当支付的相应全部款项至甲方还款账户。</p>
        <p>2、甲方应通过类似点击“还款”的方式进行还款操作，按时足额将还款金额支付至甲方还款账户，由于余额不足、账户问题、甲方操作失误等原因导致划款失败的由甲方承担相应责任。</p>
        <p>3、甲方不可撤销地授权丙方、丁方及其合作方向银行／第三方支付机构发出划款指令，从甲方还款账户中划扣相当于当期甲方应还款项的全部金额，第三方支付机构在接到划款指令后成功划转还款至乙方账户的视作甲方还款成功，由于账户余额不足、账户原因、甲方操作失误等原因导致划款失败的由甲方承担相应责任。</p>
        <p>4、乙方不可撤销地授权丙方、丁方向合作银行/第三方支付机构发出划款指令，将还款本息支付至乙方账户。甲方不可撤销地委托丁方收取丙方从乙方账户划款的到期本息金额。</p>
        <h3>第三条 逾期还款</h3>
        <p>1、甲方未能在任何一个还款日24：00前足额支付还款金额至甲方还款账户的，视作逾期还款。</p>
        <p>2、甲方逾期还款的，自还款日后第1日起向丙方支付逾期管理费，具体以甲方与丙方签订的《平台服务协议》为准。</p>
        <p>3、甲方发生逾期未还情形的，由丙方承担甲方在本协议项下所负债务（包括但不限于本金、利息、逾期罚息）。丙方构成并存的债务承担，无须乙方另行授意。丙方代为履行还款义务后，原借贷关系自动消灭。</p>
        <p>4、甲方发生逾期超过3日，甲方个人信息将可能进入信用征信系统，由此产生的后果由甲方承担。</p>
        <p>5、甲方发生逾期情形的，乙方在此不可撤销的授权丙方及丁方以丙方名义进行催收，并授权丙方及丁方可转授权给丙方及丁方的合作方催收，催收方式包括但不限于：发送网站、APP推送通知，发送短信，发送函件、电话催收，拜访催收，提起诉讼/仲裁等。</p>
        <h3>第四条 提前还款</h3>
        <p>1、甲方可以在丙方平台发起提前还款服务申请，服务是否可用以操作界面展示为准（受银行结算等原因影响，可能无法发起申请）。</p>
        <p>2、甲方选择提前还款的：甲方应一次性全额偿还按本协议应还的款项。</p>
        <h3>第五条 债权、债务转让</h3>
        <p>1、未经乙方或乙方委托丙方、丁方事先书面（包括但不限于电子邮件等方式）同意，甲方不得将本协议项下的任何权利义务转让给任何合同外第三方。</p>
        <p>2、乙方可通过签订《债权转让协议》向第三方转让本协议项下权利义务，转让后甲方即向新的债权人履行本协议项下全部义务。</p>
        <p>3、乙方向任一第三方转让本协议项下的任何权利义务的，以有效方式通知甲方及丙方（因丙方可能作为甲方的代理人），以便履行债务。</p>
        <h3>第六条 费用</h3>
        <p>甲方、乙方已充分知晓丙方、丁方提供本协议所约定的服务可能向甲方收取的费用（不包括本金、利息）并由甲方与丙方签署《平台服务协议》加以确认。</p>
        <h3>第七条 承诺与保证</h3>
        <p>1、 甲方及乙方各自在此确认其为具有完全民事权利能力和完全民事行为能力的主体，有权签订并履行本协议，并充分知晓民间借贷行为可能存在的各类风险。</p>
        <p>2、甲方使用以本人名义注册的经过实名认证并设置了密码的个人账户在贷款平台所从事的所有行为均视为甲方本人行为，包括但不限于订立本合同、申请支用贷款和归还贷款等，该等行为的法律后果均由甲方本人承担。</p>
        <p>3、乙方保证其所用于出借的资金来源合法，且乙方是该资金的合法支配权人，如第三方对资金归属、支配权、合法性等问题主张异议且获得支持的，与本协议项下借贷关系无关，乙方应当就其给他方造成的损失赔偿损失。</p>
        <p>4、甲方、乙方应及时并如实向对方及丙方、丁方、其他第三方（银行/第三方支付机构）提供本人信息（包括但不限于姓名、身份证号、企业或组织名称、统一社会信用代码、联系方式、联系地址、职业信息、联系人信息等）以及借款用途等相关信息。</p>
        <p>5、甲方、乙方承诺并保证向其他各方提供的所有本人信息均真实、完整、有效、及时，不存在虚假记载、重大遗漏或误导性陈述。因上述任何本人信息及资料的变更、修改、停用等，至少提前2个工作日通知其他各方。</p>
        <p>6、如甲方或乙方变更账户信息（账户名称、账号等）、通讯地址的，应当至少在款项交付日前2个工作日通知丙方和丁方。如因未能遵守上述承诺而导致的损失，由其自行承担。</p>
        <p>7、甲方承诺如发生任何影响或者可能影响甲方经济状况、信用状况、还款能力的事由，包括但不限于甲方的工作单位、职位、工作地点、薪酬等事项的变化，甲方应于前述变更发生之日起2个工作日内通知丙方及丁方。如发现：1)甲方在丙方系统中的行为数据或其他信息发生异常变化;或2)甲方在关联公司网站上注册的账户出现异常现象;或3)甲方发生本合同约定的违约情形；或4)甲方不履行或违反与丙方或其关联公司的其他任何合同（如有）项下甲方应承担的任何义务，已经或可能影响甲方履行本合同项下义务，丙方有权基于合理怀疑停止向甲方发放贷款或宣布贷款提前到期或对甲方的账户进行权限限制或扣划相应款项用于偿还本合同项下欠款。</p>
        <p>8、甲方承诺根据本协议列明的借款用途使用借款资金，并保证不挪用借款资金或将借款资金用于以下目的和用途：</p>
        <p>（1）以任何形式进入证券市场，或用于股本权益性投资；</p>
        <p>（2）用于房地产项目开发；</p>
        <p>（3）用于赌博；</p>
        <p>（4）用于国家法律法规明令禁止或限制的各项其他活动。</p>
        <p>9、各方承诺，各方不会利用丙方、丁方平台进行信用卡套现、洗钱、非法集资或其他不正当交易行为，否则应依法独立承担法律责任。</p>
        <p>10、各方确认，甲方和乙方授权和委托丙方、丁方根据本协议所采取的全部行动和措施的法律后果均归属于甲方和乙方本人；在任何情形下，丙方，丁方不是本协议项下任何借款或债务的债务人，亦无需以其自有资金偿还本协议项下的任何借款或最终债务。</p>
        <p>11、各方确认，民事借贷关系和其法律后果归属于甲乙双方，丙、丁分别为甲、乙提供居间服务。在发生按协议约定的债务承担以及乙方债权转让之前，丙、丁不是本协议下的债务人或债权人，不承担任何相关的法律责任。</p>
        <p>12、各方确认，丙方、丁方或丙方、丁方的合作方因计算机系统维护、故障、黑客攻击、电信部门技术调整或故障、网站升级、银行、第三方支付平台或金融相关等部门的意外事件、法律法规及政策调整、不可抗力等状况导致无法正常运作，使甲方、乙方无法正常使用各项本协议项下服务时，丙方、丁方或丙方、丁方的合作方不承担由此产生的任何违约责任及侵权责任。</p>
        <p>13、各方知晓并认可丙方、丁方与银行、第三方支付机构的合作，确认并同意因资金在途或划转延迟等可能产生的利息收益归丙方所有。</p>
        <h3>第八条 违约</h3>
        <p>1、发生下列任何一项或几项情形的，视为甲方违约：</p>
        <p>（1）甲方违反其在本协议所做的任何承诺和保证的；</p>
        <p>（2）甲方未按协议约定用途使用借款的；</p>
        <p>（3）甲方提供虚假的信息。</p>
        <p>（4）甲方的任何财产遭受没收、征用、查封、扣押、冻结等可能影响其履约能力的不利事件，且不能及时提供有效补救措施或担保的；</p>
        <p>（5）甲方的财务状况出现影响其履约能力的不利变化，且不能及时提供有效补救措施或担保的。</p>
        <p>（6）借款人被宣告失踪、处于限制民事行为能力或丧失民事行为能力状态、被刑事监禁、或发生重大疾病、重大事故等可能危及本合同项下贷款安全的情况。</p>
        <p>（7）借款人卷入或即将卷入重大的诉讼或仲裁程序及其他法律纠纷，足以影响借款人的偿债能力。</p>
        <p>（8）借款人转移资产，以逃避债务的。</p>
        <p>（9）其他影响借款人履行能力或可能危机本合同项下贷款安全的情况。</p>
        <p>2、若甲方违约或丙方/丁方合理判断甲方可能发生违约事件的，乙方及/或乙方的债权受让人（或授权丁方）有权采取下列任何一项或几项救济措施，包括但不限于：</p>
        <p>（1）立即暂缓、取消发放全部或部分借款；</p>
        <p>（2）宣告已发放借款全部提前到期，甲方应立即偿还所有应付款项；</p>
        <p>（3）解除本协议；</p>
        <p>（4）采取法律、法规以及本协议约定的其他救济措施。</p>
        <p>3、丙方向乙方及/或乙方的债权受让人进行偿付后，乙方、丁方应积极协助丙方向甲方追偿（包括但不限于签署、提供相应书面材料等），若因乙方及/或乙方的债权受让人的原因导致丙方不能追偿的，乙方及/或乙方的债权受让人应向丙方承担赔偿责任或返还偿付款项。</p>
        <h3>第九条 证据和计算</h3>
        <p>1、本协议各方确认并同意，由丙方/丁方对本协议项下所涉的任何金额进行计算；丙方/丁方可通过其自身、合作方及合作的银行/第三方支付机构提供的电子数据信息进行计算、统计，对法院需要认定的必要费用提供财务凭证。在无明显错误的情况下，上述针对本协议项下任何金额的任何证明或确定，应作为该金额有关事项的终局证明。</p>
        <p>2、甲乙双方委托丙方、丁方对相关金额进行计算，并在丙方、丁方平台发布或更新具体信息。上述还款明细中列明的每月还款本息金额若与发布或更新的还款明细不一致的，以平台上发布或更新的还款明细为准。</p>
        <h3>第十条 保密条款</h3>
        <p>1、各方应将其在本协议及其附属合同、文件的签订和履行过程中取得的有关内容以及与此等内容有关的任何文件、资料或数据信息视为保密信息（以下简称“保密信息”）。</p>
        <p>2、任意一方向其他各方承诺，其不会非法使用或向非本协议方披露保密信息，除非事先得到其他三方的书面同意或授权。</p>
        <p>3、下述信息不适用于保密信息的披露：</p>
        <p>（1）该等信息已为公众所知；</p>
        <p>（2）任何适用法律要求披露的、或者有权的司法机关、政府机关、监管机关要求披露的、或者法院裁定要求披露的信息；</p>
        <p>（3）相关方在正当履行本合同时披露的或随本合同而相应披露的信息；</p>
        <p>（4）相关方从第三方获得的且无须承担保密义务的信息。但是上述第（3）项或第（4）项信息的披露必须基于接收方对上述信息保密并且接收方仅可为披露之目的使用上述信息。</p>
        <p>4、保密信息应绝对保密，未经本协议各方以书面方式一致同意，任何一方不得向除政府审批机构、顾问（包括律师、会计师、评估师和其他专业顾问）、为本合同的交易提供服务的机构、股东和相关工作人员之外的任何与本合同无利害关系的第三人披露本合同任何条款。</p>
        <p>5、本协议项下的任何一方应采取所有其他必要、适当的措施，以确保保密信息的保密性。</p>
        <p>6、不论本协议是否有效或者是否履行完毕，本条中规定的保密义务不因此受到任何影响。</p>
        <p>7、保密条款在本协议合法解除、终止或履行完毕后继续有效。</p>
        <h3>第十一条 知识产权</h3>
        <p>1、丙方、丁方及关联公司所有系统、提供的服务、本协议所有内容，包括但不限于产品名、商标、网站名、网站架构、网站画面、网站设计、著作、图片、档案等无形资产（包括但不限于商标权、专利权、著作权、商业秘密等）均由丙方、丁方或关联公司所有。</p>
        <p>2、非经丙方、丁方或关联企业书面同意，任何人不得擅自使用、修改、反向工程、反汇编、反编译、复制、公开传播、散布、发行或公开发表丙方、丁方程序或内容。</p>
        <p>3、尊重知识产权是甲方、乙方应尽的义务，任何人侵犯了丙方、丁方和/或关联公司的知识产权，应当承担损害赔偿责任，被侵害方拥有追究责任的权利。</p>
        <h3>第十二条 通知</h3>
        <p>1、本协议任何一方根据本协议约定做出的通知和/或文件均应以书面形式做出（不包括因贷后管理服务所涉内容），可由专人送达、挂号邮递、特快专递或通过丙方网络平台发布等方式传送，具体送达信息以本协议约定或各方在丙方、丁方平台的注册信息或登记信息为准。</p>
        <p>2、通知在下列日期视为送达：</p>
        <p>（1）短信、邮件发出即视为送达，专人递送的通知，在专人递送之交付日为有效送达；</p>
        <p>（2）以挂号信（付清邮资）发出的通知，在寄出（以邮戳为凭）后的五（5）个工作日内为有效送达；</p>
        <p>（3）以特快专递（付清邮资）发出的通知，在寄出（以邮戳为凭）后的三个（3）工作日内为有效送达；</p>
        <p>（4）通过丙方网络平台发布的方式通知的，在丙方网络平台发布之日为有效送达。</p>
        <h3>第十三条 法律适用和管辖</h3>
        <p>本合同适用中华人民共和国法律。如果各方在本协议履行过程中发生争议，可向上海市长宁区有管辖权的人民法院提起诉讼，因诉讼产生的诉讼费、律师代理费等相关费用由败诉方承担。诉讼期间，本合同不涉及争议部分的条款仍须继续履行。</p>
        <h3>第十四条 其他</h3>
        <p>1、甲方希望通过丙方、丁方运营平台向乙方借款，乙方亦同意借款。各方协商一致，达成本协议。</p>
        <p>2、本协议自借款向借款人实际放款之日起生效。</p>
        <p>3、本协议中部分条款根据相关法律法规等的规定成为无效，或部分无效时，该等无效不影响本协议项下其他条款的效力，各方仍应履行其在本协议项下的义务。</p>
        <p>4、本协议项下的附件和补充协议构成本协议不可分割的一部分，具有同等法律效力。</p>
        <p>5、本协议各方委托丙方、丁方保管所有与本协议有关的书面文件或电子信息；本协议各方确认并同意由丙方、丁方提供的与本协议有关的书面文件或电子信息在无明显错误的情况下应作为本协议有关事项的终局证明。</p>
        <p>6.本协议以中文签署，一式八份，具有同等法律效力，各方各执贰份。本协议自各方盖章之日起生效。</p>
        <div className="contact-icon">
            <p>甲方（借款人）：{loanList.realName}</p>
            <p>乙方（出借人）：{loanList.partBRealName}</p>
            <p>丙方（居间方/ 担保方）： {loanList.companyTitle}</p>
            <p>丁方（居间方）：  {loanList.capitalName}</p>
            <p>协议日期：{loanList.loanTime}</p>
            <img src={appName == 'jsxjx'? require('../../images/contact_Icon.png'):require('../../images/contact_Icondk.png')} style={{display:JSON.stringify(this.state.loanList) == "{}"?'none': 'block'}} className="contact-img" alt=""/>
        </div>
      </div>
    );
  }
}




